const useGetExistingPlan=async({domain,channel})=>{

    const requestOptions = {
        method: "GET",
        redirect: "follow"
      };
      let response = await fetch("https://api.cedcommerce.com/pricing-dev/getSubscription?domain="+domain+"&channel="+channel+"&mode=_sandbox", requestOptions)
      let subscription = await response?.json();
     return subscription;
    }
export default useGetExistingPlan;