const useChargebee = () => {
  let cbInstance;
    const loadChargebee = () => {
      if (window.Chargebee) {
        cbInstance = window.Chargebee.init({
          site: 'cedcommerce-test'
        });
      }
    };

    if (document.readyState === 'complete') {
      loadChargebee();
    } else {
      window.addEventListener('load', loadChargebee);
      return () => window.removeEventListener('load', loadChargebee);
    }

  return cbInstance;
};
export default useChargebee;
