const useUserGet=async({domain,channel})=>{

    const requestOptions = {
        method: "GET",
        redirect: "follow"
      };
      
      let response = await fetch("https://api.cedcommerce.com/pricing-dev/getUser?domain="+domain+"&channel="+channel+"&mode=_sandbox", requestOptions)
      let users = await response.json();
     return users;
    }
export default useUserGet;