import * as React from 'react';
import Button from '@mui/joy/Button';
import usechargebee from '../utils/ConfigureChargebee';
import usecheckout from '../utils/useCheckout';
import useuserPost from '../Api/useUserPost';
import useuserGet from '../Api/useUserGet';
import useupdatePlan from '../utils/useUpdatePlan';
import { useEffect, useState, useRef } from 'react';
import usegetPlans from '../Api/useGetPlans';
import { Radio } from 'antd';
import SkeletonUI from './SkeltonUI';
import ExistingPlan from './ExistingPlan';
import usegetExistingPlan from '../Api/useGetExistingPlan';
import FreePlan from './FreePlan';
import { useLocation } from "react-router-dom";
const PricingSEA = () => {
    const [period, setPeriod] = useState('monthly');
    const [plandata, setPlanData] = useState({});
    const [existplan, setExistPlan] = useState({});
    const [allplans, setAllplans] = useState({});
    const [queryparam, setqueryparam] = useState({});
    const [loading, setloading] = useState(true);
    const [showexitplan, setshowexitplan] = useState(false);
    const [shownewplan, setshownewplan] = useState(true);
    const [hidefreeplan, sethidefreeplan] = useState(false);
    const [FreePlanpopup, setFreePlanpopup] = useState(false);
    const location = useLocation();
    const getplans = async () => {
        let data = await usegetPlans();
        setAllplans(data);
        setPlanData(data?.[period]);
        setloading(false);
    }

    const getplansupdated = async () => {
        let data = await usegetPlans();
        delete data?.[period]['free'];
        setAllplans(data);
        setPlanData(data?.[period]);
        setloading(false);
    }
    const ref = useRef(null);

    useEffect(() => {
        const getQueryParams = async () => {
            const query = new URLSearchParams(window.location.search);
            const domain = query.get('domain');
            const channel = query.get('channel');
            setqueryparam({ domain: domain, channel: channel })
            let exist = await getExitingPlans({ domain: domain, channel: channel })
            if (!exist) {
                await getplans();
            } else {
                await getplansupdated();
            }

        };
        getQueryParams();

    }, []);
    const getExitingPlans = async ({ domain, channel }) => {
        let responce = await usegetExistingPlan({ domain, channel });
        //console.log("response----23435",responce);
        if (responce?.status == true) {
            setExistPlan(responce?.data);
            setshowexitplan(true);
            setshownewplan(false);
            sethidefreeplan(true);
            return true;
        } else {
            return false;
        }
    }
    const changePlantype = (e) => {
        let period = e.target.value;
        if (!hidefreeplan) {
            setPlanData(allplans?.[period]);
        } else {
            delete allplans?.[period]['free']
            setPlanData(allplans?.[period]);
        }
        setPeriod(period);
    }
    const show = async (plan_id) => {
        setloading(true);
        if (plan_id == "woo_free_plan") {
            setFreePlanpopup(true);
            setloading(false);
            return false;
        }
        let cbInstance = usechargebee();
        let user = await useuserGet(queryparam);
        // console.log("user--", user);
        if (user?.status == true && user?.data?.plan_type != 'free' && user?.data?.state == 'active') {
            const { subscription_id, status } = user?.data
            // console.log("subscription_id--", subscription_id);
            useupdatePlan(cbInstance, subscription_id, queryparam, plan_id)
        } else {
            useuserPost(queryparam);
            let status = await usecheckout(cbInstance, queryparam, plan_id);
        }
        setloading(false);
    };
    const showfreeplan = () => {
        setFreePlanpopup(!FreePlanpopup);
    }
    const optionsWithDisabled = [
        {
            label: 'Monthly',
            value: 'monthly',
        },
        {
            label: 'Yearly',
            value: 'yearly',
        }
    ]
    const handleClick = async () => {
        await setshownewplan(true);
        ref.current?.scrollIntoView({ behavior: 'smooth' });
    };
    return (
        <>
            {

                showexitplan && <ExistingPlan updateplan={handleClick} existplan={existplan} />
            }
            {
                shownewplan &&

                <div className="ced-pricing-page-container " ref={ref}>
                    <div className="ced-page-plan-content ">
                        <div className="ced-pricing-plan-common-header">
                            <div className="ced-pricing-plan-header-content">
                                {/* <h2>Choose your right plan!</h2>
            <p></p> */}
                            </div>
                        </div>
                        <div className="plan-selector-wrapper" >
                            <div className="ced-pricing-container-common">
                                <div className="ced-sricth-common-wrap">
                                    {/* {loading ? <SkeletonUI/> : */}
                                    <Radio.Group
                                        options={optionsWithDisabled}
                                        onChange={changePlantype}
                                        value={period}
                                        optionType="button"
                                        buttonStyle=""
                                        style={{ position: 'sticky' }}
                                    />
                                    {/* } */}
                                </div>
                            </div>
                        </div>
                        <div className="ced-pricing-card-common-wrapper">
                            {
                                FreePlanpopup && <FreePlan showfreeplan={showfreeplan} queryparam={queryparam} />
                            }
                            <div className="ced-pricing-card-common ">
                                {loading ? <SkeletonUI /> :

                                    Object?.keys(plandata)?.map((oneKey, i) => {
                                        return <div className="ced-pricing-marketplace-card-container">
                                            <div className="ced-pricing-basic-card">
                                                <div className="ced-card-basic-card-common">
                                                    <label>
                                                        <h3>{plandata[oneKey]?.plan_name}</h3>
                                                        <h2><span className="ced-pricing-value">${plandata[oneKey]?.plan_price}</span>/month</h2>
                                                        {
                                                            period == "yearly" ? <h5><span>Billed Anually ${plandata[oneKey]?.price_total}</span></h5> :
                                                                <h4><span></span></h4>
                                                        }
                                                        <div className="ced-card-property-common">
                                                            <ul>
                                                                <li>{plandata[oneKey]?.description1}</li>
                                                                <li>{plandata[oneKey]?.description2}</li>
                                                            </ul>
                                                        </div>
                                                        <div className="ced-pricing-button-common">
                                                            <Button className='subscribe-button' onClick={() => { show(plandata[oneKey]?.plan_id) }} style={{ backgroundColor: '#674399', position: "unset" }} type="button">Select Plan</Button>
                                                        </div>
                                                    </label>
                                                </div>
                                            </div>
                                        </div>
                                    })
                                }
                            </div>
                        </div>
                    </div>
                </div>
            }
        </>
    )
}

export default PricingSEA;