import React from "react";
import { useNavigate } from "react-router-dom";
const useCheckout=async(cbInstance,queryparam,plan_id)=>{
    const { domain, channel } = queryparam;
    const basePath = window.location.origin;
    console.log("basePath",basePath);
    let response = await fetch("https://api.cedcommerce.com/pricing-dev/ChargebeeCheckout", {
      method: "POST",
      body: JSON.stringify({ plan_id: plan_id, domain: domain, channel: channel }),
    })
    let data = await response.json();
    if (data?.status == true) {
      // window.location.href = data?.hostedpage;
      try {
        cbInstance.openCheckout({
          hostedPage: async() => {
          return await { url: data?.hostedpage };
          },
          success: async(hostedPageId) => {
            window.location.href = basePath+"/pricing/success?domain="+domain+"&channel="+channel;
          }
          ,
          close: () => {
            console.log('Checkout was closed');
          }
        });
      } catch (error) {
        console.error('Error fetching hosted page:', error);
      }
    }
    return data;
 }
export default useCheckout;