
const useUpdatePlan = async (chargebee, subscription_id, queryparam, plan_id) => {
  const { domain, channel } = queryparam;
  const basePath = window.location.origin;
  let response = await fetch("https://api.cedcommerce.com/pricing-dev/updateChargebeeSubscription", {
    method: "POST",
    body: JSON.stringify({ subscription_id: subscription_id, plan_id: plan_id, domain: domain, channel: channel }),
  })
  let data = await response.json();
  if (data?.status == true) {
    // window.location.href = data?.hostedpage;
    try {
      chargebee.openCheckout({
        hostedPage: async() => {
        return await { url: data?.hostedpage };
        },
        success: (hostedPageId) => {
          window.location.href = basePath+"/pricing/success?domain="+domain+"&channel="+channel;
          // Handle success (e.g., show a success message, refresh data)
        },
        close: () => {
          console.log('Checkout was closed');
        }
      });
    } catch (error) {
      console.error('Error fetching hosted page:', error);
    }
  }
  return data;
}
export default useUpdatePlan;