const useUserPost=({domain,channel})=>{

    const requestOptions = {
        method: "GET",
        redirect: "follow"
      };
      
      fetch("https://api.cedcommerce.com/pricing-dev/addUser?domain="+domain+"&channel="+channel+"&mode=_sandbox", requestOptions)
        .then((response) => response.text())
        .then((result) => console.log(result))
        .catch((error) => console.error(error));
}
export default useUserPost;