import {useEffect, useRef, useState} from 'react';
const ExistingPlan = ({updateplan,existplan}) => {
    const[exitdata,setexistdata]=useState({});
    const handleClick=()=>{
        updateplan(); 
    }
    useEffect(()=>{
      console.log("existplan----",existplan);
      if(existplan?.plan_name=="Free"){
        setexistdata({
          next_payment_date:'-',
          end_date:'-',
          Name:existplan?.plan_name,
          billing_period:'-',
          price:0,
          status:existplan?.status,
          allowed_orders:existplan?.allowed_orders,
          allowed_products:existplan?.allowed_products
      });
      }else{
          const{billing_intents={},next_payment_date='',end_date='',status=''}=existplan?.data;
          const{Name,billing_period,price}=billing_intents;
          setexistdata({
              next_payment_date:next_payment_date,
              end_date:end_date,
              Name:Name,
              billing_period:billing_period,
              price:price,
              status:status
          });
      }
    },[])
    return (
        <>
    <div className="ced-pricing-plan-wrapper">
      <div className="ced-pricing-plan-wrap">
        <div className="ced-pricing-plan-container">
          <div className="ced-pricing-plan-common-wrapper">
            <h3>Current Plan Details</h3>
          </div>
          <div className="ced-pricing-plan-details-container">
            <div className="ced-pricing-plan-details-wrap">
              <table>
                <tbody>
                  <tr>
                    <td>Plan Status</td>
                    <td>:</td>
                    <td><span className="ced-active">{exitdata?.status}</span></td>
                  </tr>
                  <tr>
                    <td>Plan Name</td>
                    <td>:</td>
                    <td>{exitdata?.Name}</td>
                  </tr>
                  <tr>
                    <td>Plan Price</td>
                    <td>:</td>
                    <td>${exitdata?.price}</td>
                  </tr>
                  <tr>
                    <td>Billing Period</td>
                    <td>:</td>
                    <td>{exitdata?.billing_period}</td>
                  </tr>
                  <tr>
                    <td>End Date</td>
                    <td>:</td>
                    <td>{exitdata?.end_date}</td>
                  </tr>
                  <tr>
                    <td>Next Payment Date</td>
                    <td>:</td>
                    <td>{exitdata?.next_payment_date}</td>
                  </tr>
                  <tr>
                    <td>Active Order</td>
                    <td>:</td>
                    <td>{exitdata?.allowed_orders}</td>
                  </tr>
                  <tr>
                    <td>Active Products</td>
                    <td>:</td>
                    <td>{exitdata?.allowed_products}</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div className="ced-pricing-plan-action-container">
              <div className="ced-pricing-action-buttons">
                <button className="ced-update" onClick={handleClick}>Update</button>
                <button className="ced-cancel">Cancel</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
        </>
    )
}
export default ExistingPlan;