import { Spin } from 'antd';
import { useEffect } from 'react';
import { useNavigate } from "react-router-dom";
const Success = () => {
    const navigate = useNavigate();
    const gettoken=()=>{
        const query = new URLSearchParams(window.location.search);
        const domain = query.get('domain');
        const channel = query.get('channel');
    //   const {domain , channel } = query.get('query');
        setTimeout(() => {
            const gettoken = async () => {
                let response = await fetch("https://api.cedcommerce.com/pricing-dev/getaccesstoken?domain=" + domain + "&mode=_sandbox&channel=" + channel + "")
                let data = await response?.json();
                let token = data?.access_token;
                   // Data to send
               const tokenmess = {
                status: true,
                token: token
            };
            window.parent.postMessage(tokenmess, '*');
            // Send data to the parent window
            
               // navigate("/pricing");
              // parent.location.href = domain+"/wp-admin/admin.php?page=sales_channel&channel=shopee&token="+token;
            }
            gettoken();
        }, 5000)
    }
    useEffect(()=>{
        gettoken();
    },[])
    return (
        <div>
            <Spin />
        </div>
    )
}
export default Success;